@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --Very-Light-Gray: hsl(0, 0%, 98%);
  --Very-Light-Grayish-Blue: hsl(236, 33%, 92%);
  --Light-Grayish-Blue: hsl(233, 11%, 84%);
  --Dark-Grayish-Blue: hsl(236, 9%, 61%);
  --Very-Dark-Grayish-Blue: hsl(235, 19%, 35%);
  --Bright-Blue: hsl(220, 98%, 61%);
}

body {
  font-family: 'Josefin Sans';
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: column;
}

.bg-image {
  width: 100%;
  position: fixed;
  z-index: -10;
  top: 0;
  left: 0;
  position: absolute;
}

.bg-image img {
  width: 100%;
  object-fit: cover;
}

header {
  display: flex;
  width: 550px;
  justify-content: space-between;
  align-items: center;
}

h1 {
  color: white;
  letter-spacing: 15px;
  font-size: 40px;
}

img {
  -webkit-user-select: none;
          user-select: none;
}

.white {
  background-color: white;
}

.input-area {
  display: flex;
  width: 520px;
  align-items: center;
  border-radius: 5px;
  height: 60px;
  padding: 0 15px;
  box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.1);
}

input {
  margin-top: 3px;
  font-family: 'Josefin Sans';
  background-color: transparent;
  border: none;
  padding: 0 10px;
  font-size: 16px;
  width: 100%;
  color: hsl(235, 19%, 35%);
  color: var(--Very-Dark-Grayish-Blue);
}

input::-webkit-input-placeholder {
  color: hsl(236, 9%, 61%);
  color: var(--Dark-Grayish-Blue);
}

input::placeholder {
  color: hsl(236, 9%, 61%);
  color: var(--Dark-Grayish-Blue);
}

input:focus-visible {
  outline: none;
}

.check {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: white;
  border: 1px solid hsl(236, 33%, 92%);
  border: 1px solid var(--Very-Light-Grayish-Blue);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.true-check {
  background: linear-gradient(
    135deg,
    hsl(192, 100%, 67%),
    hsl(280, 87%, 65%)
  );
}

.tasks-area {
  margin: 20px 0;
  border-radius: 5px;
  box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.1);
  padding: 0 15px;
  overflow: hidden;
}

.tasks-footer {
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
  font-size: 13px;
  color: hsl(236, 9%, 61%);
  color: var(--Dark-Grayish-Blue);
}

.selectors span, .clear-link {
  margin: 0 5px;
  cursor: pointer;
}

.task-content {
  display: flex;
  height: 60px;
  align-items: center;
}

.task-content span {
  margin: 3px 10px 0;
  color: hsl(235, 19%, 35%);
  color: var(--Very-Dark-Grayish-Blue);
  width: 100%;
}

hr {
  width: 120%;
  margin: 0 -10%;
  border: 1px solid hsl(236, 33%, 92%);
  border: 1px solid var(--Very-Light-Grayish-Blue);
}

.task-content span.completed {
  color: hsl(233, 11%, 84%);
  color: var(--Light-Grayish-Blue);
  text-decoration: line-through;
}

.cross {
  cursor: pointer;
  visibility: hidden;
}

.task:hover .cross{
  visibility: visible;
}

.selectors {
  font-weight: bold;
}

.clear-link:hover {
  color: hsl(235, 19%, 35%);
  color: var(--Very-Dark-Grayish-Blue);
}

.selectors span:hover {
  color: hsl(235, 19%, 35%);
  color: var(--Very-Dark-Grayish-Blue);
}

.selectors span.active-filter {
  color: hsl(220, 98%, 61%);
  color: var(--Bright-Blue);
}
:root {
  --Very-Light-Gray: hsl(0, 0%, 98%);
  --Very-Light-Grayish-Blue: hsl(236, 33%, 92%);
  --Light-Grayish-Blue: hsl(233, 11%, 84%);
  --Dark-Grayish-Blue: hsl(236, 9%, 61%);
  --Very-Dark-Grayish-Blue: hsl(235, 19%, 35%);
  --Bright-Blue: hsl(220, 98%, 61%);
}

@media (hover: none) {
    
	.cross {
		visibility: visible;
	}
}

@media (max-width: 600px) {

	body {
		min-height: auto;
	}

	#root {
		width: 100%;
	}

	.App {
		width: 100%;
	}

	header {
		width: 90%;
		padding: 0 5%;
		margin-top: 20px;
	}

	main {
		margin: 0 5%;
	}

	.input-area {
		width: 90%;
		padding: 0 5%;
	}

	.tasks-area {
		padding: 0 5%;
	}

	.selectors {
		background-color: white;
		box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.1);
		height: 60px;
		display: flex;
		align-items: center;
		justify-content: center;
		color: hsl(236, 9%, 61%);
		color: var(--Dark-Grayish-Blue);
	}
}
